.about-container-global {
    background-image: linear-gradient( to bottom , #2c2c2c , #4b1b5e);
    height: 1150px;
}

.about-container {
    background: url(/static/media/manFace.d6c3d0af.png) left top no-repeat,linear-gradient( to bottom , #2c2c2c , #4b1b5e);
    background-size: 365px;
    max-width: 1200px;
    margin: auto;
    display: block;
}

.about-heading{
    font-family: "Josefin Sans";
    font-weight: 700;
    font-size: 150px;
    color: rgb(209, 202, 202);
    padding-top: 20px;
    
}

.about-main-grid {
    height: 820px;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: space-around;
    justify-content: space-around;
}

.about-p {
    height:100%;
    width: 100%;
    max-width: 400px;
    border-radius: 3px;
    font-family: 'Ralewaylight';
    font-size: 18px;
    font-weight: 100;
    color: #ffffff;
    padding: 0 10px 0 10px;
     margin-left: -20px;
     display: flex;
    flex-direction: column;
    align-items: center;
    align-content: space-around;
    text-align: justify;
    line-height: 1.5;
   
}

.about-first-para,.about-second-para{
    padding: 100px 0 20px 0;
}

.about-first-para{
    position: relative;
    right: -100px;
}

@media screen and (max-width: 992px) {
    .about-first-para{
      position: relative;
      right: -100px;
    }
}

@media screen and (max-width: 870px) {
    .about-first-para{
      position: relative;
      right: -150px;
    }
}

@media screen and (max-width: 750px) {
    .about-container {
        background-size: 300px;
    }

    .about-first-para{
      position: relative;
      right: 0;
    }
    .about-p {
        padding: 300px 20px 0 20px;
        font-size: 15px;
    }
    .about-heading {
        font-size: 100px
    }
}


@media screen and (max-width: 450px) {
    .about-p {
        padding: 150px 20px 0 20px;
        font-size: 15px;
    }
}
